.Intro {
  background-color: white;
  display: flex;

  .left {
    flex: 0.5;

    .imageContainer {
      width: 700px;
      height: 700px;
      background-color: rgb(172, 226, 172);
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      float: right;

      img {
        height: 100%;
        border-radius: 50%;
      }
    }
  }

  .right {
    flex: 0.5;
    position: relative;
    .wrapper {
      width: 100%;
      height: 100%;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      h1 {
        font-size: 60px;
        margin: 10px 0;
      }
      h2 {
        font-size: 35px;
      }
      h3 {
        font-size: 30px;
        span {
          font-size: inherit;
          color: crimson;
        }
      }
    }
  }
  a {
    position: absolute;
    bottom: 10px;
    left: 40%;
    img {
      width: 30px;
      animation: arrowBlink 2s infinite;
    }
    @keyframes arrowBlink {
      100% {
        opacity: 0;
      }
    }
  }
}
